.wc-block-checkout__payment-method {
  .wc-stripe-label-container {
    .wc-block-components-payment-method-icons {
      .wc-block-components-payment-method-icon {
        max-width: 100% !important;
      }
    }

    &.stripe_ideal {
      img {
        &.wc-block-components-payment-method-icon--stripe_ideal {
          height: 29px;
          max-height: 29px;
        }

      }
    }

    &.stripe_p24 {
      img {
        &.wc-block-components-payment-method-icon--stripe_p24 {
          height: 25px;
          max-height: 25px;
        }

      }
    }

    &.stripe_bancontact {
      img {
        &.wc-block-components-payment-method-icon--stripe_bancontact {
          height: 26px;
          max-height: 26px;
        }

      }
    }

    &.stripe_eps {
      img {
        &.wc-block-components-payment-method-icon--stripe_eps {
          height: 34px;
          max-height: 34px;
        }

      }
    }

    &.stripe_multibanco {
      img {
        &.wc-block-components-payment-method-icon--stripe_multibanco {
          height: 45px !important;
          max-height: 45px !important;
        }

      }
    }

    &.stripe_sepa {
      img {
        &.wc-block-components-payment-method-icon--stripe_sepa {
          height: 17px;
          max-height: 17px;
        }

      }
    }

    &.stripe_sofort {
      img {
        &.wc-block-components-payment-method-icon--stripe_sofort {
          height: 22px;
          max-height: 22px;
        }

      }
    }

    &.stripe_fpx {
      img {
        &.wc-block-components-payment-method-icon--stripe_fpx {
          height: 22px;
          max-height: 22px;
        }

      }
    }

    &.stripe_alipay {
      img {
        &.wc-block-components-payment-method-icon--stripe_alipay {
          height: 20px;
          max-height: 20px;
        }

      }
    }

    &.stripe_klarna {
      img {
        &.wc-block-components-payment-method-icon--stripe_klarna {
          height: 26px;
          max-height: 26px;
          max-width: 48px;
        }

        &[src$="klarna.svg"] {
          height: 16px;
        }

      }
    }

    &.stripe_konbini {
      img {
        &.wc-block-components-payment-method-icon--stripe_konbini {
          height: 50px;
          width: 42px;
          max-height: 50px;
        }
      }
    }

    &.stripe_paynow {
      img {
        &.wc-block-components-payment-method-icon--stripe_paynow {
          height: 30px;
          width: 75px;
          max-height: 30px;
        }
      }
    }

    &.stripe_afterpay {
      img {
        &.wc-block-components-payment-method-icon--stripe_afterpay {
          height: 20px;
          width: 100px;
          max-height: 20px;
        }

        &[src$="afterpay_mint_black.svg"],
        &[src$="clearpay_mint_black.svg"] {
          height: 32px;
          width: 92px;
          max-height: 32px;
        }
      }
    }

    &.stripe_swish {
      img {
        &.wc-block-components-payment-method-icon--stripe_swish {
          height: 28px;
          width: 90px;
          max-height: 28px;
        }
      }
    }

    &.stripe_mobilepay {
      img {
        &.wc-block-components-payment-method-icon--stripe_mobilepay {
          height: 30px;
          width: 30px;
          max-height: 30px;
        }
      }
    }

    &.stripe_cashapp {
      img {
        &.wc-block-components-payment-method-icon--stripe_cashapp {
          height: 30px;
          width: 30px;
          max-height: 30px;
        }
      }
    }

    .wc-stripe-affirm-message-container,
    .wc-stripe-afterpay-message-container {
      display: flex;
      justify-content: flex-end;
      flex: 1;
      margin-right: 8px;

      .StripeElement {
        width: 100%;
        margin-left: 30px;
      }
    }

    .wc-stripe-klarna-message-container {
      display: flex;
      justify-content: flex-end;
      width: 100%;
    }
  }
}

.wc-stripe-local-payment-container {
  border: 1px solid #dadce0;
  border-radius: 4px;
  min-height: 42px;
  display: flex;
  align-items: center;

  &.IbanElement,
  &.AuBankAccountElement {
    padding-left: 10px;
  }

  .StripeElement {
    flex: 1;
  }
}

#wc-stripe-block-qrcode {
  + p {
    margin-top: 5px;
  }
}

.wechat-validation-error {
  .wc-block-components-validation-error {
    position: relative;
  }
}

.wc-stripe-blocks-afterpay__label {
  .AfterpayMessage {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }

  .AfterpayMessage-logoSvg {
    display: inline-block;
    max-width: 90px;
  }

  .AfterpayMessage-learnMore {
    vertical-align: middle;
    font-size: 16px;
  }
}

.wc-stripe-boleto__description {
  margin-top: 10px;

  div {
    display: flex;
    align-items: center;

    label {
      margin: 0;
    }
  }
}

.wc-stripe-klarna__testmode {
  label {
    display: inline-block;
  }
}

.wc-stripe-blocks-mandate {
  margin-top: 10px;
}

.wc-stripe-blik-codes {
  display: flex;
  margin: 0 -8px;
  flex-wrap: wrap;

  span {
    margin: 8px 8px;

    input {
      width: 45px;
      height: 45px;
      text-align: center;
      font-size: 18px;
      border: 1px solid #50575e;
      border-radius: 4px;
    }
  }
}

.wc-stripe-blik-codes-container {
  padding: 0 1em;
}

.wc-stripe-blik-timer-container {
  display: flex;
  flex-direction: column;
  font-size: 1.2em;
  align-items: baseline;
  padding: 0 1em;

  .wc-stripe-blik-timer {
    display: flex;
    font-size: 1.4em;
    position: relative;

    .timer {
      margin-right: 14px;
    }

    .seconds {
      position: absolute;
      right: 0;
    }
  }
}

.wc-stripe-wechat-instructions {
  li {
    list-style: decimal;
  }
}

.wc-stripe-cart-message-container {
  .AfterpayMessage {
    margin: 0 !important;
    padding: 0;
  }

  &.stripe_affirm {
    .StripeElement {
      width: 100%;
    }
  }
}
